import { useState, useEffect } from 'react'
import { BigNumber } from 'ethers'
import { Link } from 'react-router-dom'
import Jumbo from "../components/Jumbo"
import DoomCatCard from '../components/DoomCatCard'
import { useData } from '../dataContext'
import { useWeb3 } from '../web3'

const Ascended = () => {
  const {
    totalCats, remainingCats, genesisCats
  } = useData()
  const { account } = useWeb3()

  const [rescuedCount, setRescuedCount] = useState(BigNumber.from(0))
  useEffect(() => {
    if (!remainingCats || !totalCats || !genesisCats) return
    setRescuedCount(totalCats.sub(remainingCats).sub(genesisCats))
  }, [genesisCats, remainingCats, totalCats])

  const [mostRecentXCats, setMostRecentXCats] = useState(BigNumber.from(0))
  useEffect(() => {
    const maxToShow = BigNumber.from(16)
    setMostRecentXCats(rescuedCount.gt(maxToShow) ? maxToShow : rescuedCount)
  }, [rescuedCount])

  const [catIds, setCatIds] = useState([])
  useEffect(() => {
    setCatIds([...new Array(mostRecentXCats.toNumber()).keys()].reverse().map(catIndex => {
      return BigNumber.from(catIndex).add(genesisCats).add(1).add(rescuedCount.sub(mostRecentXCats))
    }))
  }, [genesisCats, mostRecentXCats, rescuedCount])

  return (
    <Jumbo>
      <div className="mb-8 text-center text-xl italic">{rescuedCount.toString()} DoomCats have been Rescued</div>
      {account && 
        <div className="mb-8 text-center text-purple">
          <Link to="/gallery">View My DoomCats</Link>
        </div>
      }
      <div className="mb-8 text-center">{mostRecentXCats.toString()} Most Recently Rescued DoomCats</div>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-10">
        {catIds.length > 0 && catIds.map((catId, index) => {
          return (
            <DoomCatCard tokenId={catId} key={index} />
          )
        })}
      </div>
    </Jumbo>
  )
}

export default Ascended
