import { Link } from 'react-router-dom'
import { useData } from '../dataContext'
import { useCatInfo } from '../dataContext/cats'

const Container = ({ children }) => {
  return (
    <div className="transition duration-500 ease-in-out transform hover:scale-110">
      {children}
    </div>
  )
}

const LoadingError = ({ children, catId }) => {
  return (
    <div className="h-full text-center text-sm">
      <div>DoomCat #{catId.toString()}</div>
      <div className="flex flex-col justify-center h-full">{children}</div>
    </div>
  )
}

const DoomCatCard = ({ tokenId }) => {
  const { doomCatContract } = useData()
  const [catInfo, error] = useCatInfo(doomCatContract, tokenId)

  if (error) {
    return (
      <Container>
        <LoadingError catId={tokenId}>
          <div>Error: {error.message}</div>
        </LoadingError>
      </Container>
    )
  } else if (catInfo) {
    return (
      <Link to={`/ascended/${tokenId}`}>
        <Container>
          <div className="text-center text-sm mb-2">{catInfo.name}</div>
          <img width="100%" src={catInfo.image} alt={catInfo.name} />
        </Container>
      </Link>
    )
  } else {
    return (
      <Container>
        <LoadingError catId={tokenId}>
          <div>Loading...</div>
        </LoadingError>
      </Container>
    )
  }
}

export default DoomCatCard
